import React, { Component } from 'react';
import {connect} from 'react-redux';
import { bindActionCreators } from 'redux';
import * as toolsActions from '../../actions/toolsActions';
import Hand from './parts/Hand';
import Delete from './parts/Delete';
import Select from './parts/Select';
import Create from './parts/Create';
import Turn from './parts/Turn';
import ContentTool from './parts/ContentTool';

class Tools extends Component{

    render(){

        return(
            <div className="toolsContainer">
                <div className="tools">
                    <Hand toolsActions = {this.props.toolsActions} tools={this.props.tools}/>
                    <Delete toolsActions = {this.props.toolsActions} tools={this.props.tools}/>
                    <Select toolsActions = {this.props.toolsActions} tools={this.props.tools}/>
                    <Create toolsActions = {this.props.toolsActions} tools={this.props.tools}/>
                    <Turn toolsActions = {this.props.toolsActions} tools={this.props.tools}/>
                </div>
                <ContentTool tools={this.props.tools} blocks={this.props.blocks}/>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return{
        tools: state.toolsReducer,
        blocks: state.blocksReducer,
    };
}

function mapDispatchToProps(dispatch) {
    return{
        toolsActions: bindActionCreators(toolsActions, dispatch)
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Tools);